.sector_interested {
    border-radius: var(--border-radius);
    background: #fff;
    // padding: 20px;
    // margin: 0 20px 20px 20px;

    .header {
      display: flex;
      justify-content: space-between;
      // padding-bottom: 5px;
      // border-bottom: solid 1px #dfdfdf;
      align-items: center;
    }

    .interested_cards {
      display: flex;
      width: 100%;
      gap: 3em;
      overflow-x: auto;

      .interested_card {
        flex-shrink: 0;
        // margin: 10px 0;
        padding: 20px;
        min-width: 150px;
        height: 70px;
        display: flex;
        align-items: center;
        gap: 10px;
        justify-content: center;
        box-shadow: 4px 4px 30px 0px rgba(211, 243, 107, 0.2);
      }
    }

    // For responsiveness of Modal component
  .modal {
    margin-top: 20%;

    @media (min-width: 500px) {
      margin-top: 15%;
    }

    @media (min-width: 575px) {
      margin-top: 13.5%;
    }

    @media (min-width: 768px) {
      margin-top: 10%;
    }

    @media (min-width: 769px) {
      margin-top: 0;
    }
  }

   // Scrollbar
   
   .interested_cards::-webkit-scrollbar {
     height: 10px;
   }
 
   
   .interested_cards::-webkit-scrollbar-thumb {
     background-color: rgba(211, 243, 107, 0.2);
     border-radius: 10rem;
 
     &:hover {
       background-color: rgba(211, 243, 107, 1);
     }
   }
 
  
   .interested_cards::-webkit-scrollbar-track {
     background-color: transparent;
     border-radius: 10rem;
   }
  }